class AudioChannel {
    constructor(volume = 1) {
        this.volume = volume;
    }
    getVolume() {
        return this.volume;
    }
    setVolume(value) {
        this.volume = value;
    }
}

class Sound {
    constructor(fileUrl, options = {}) {
        this.initialVolume = options.volume ? options.volume : 1;
        this.initialLoop = options.loop ? options.loop : false;
        this.playing = false;
        this.Audio = new Audio(fileUrl);
        this.Audio.loop = this.initialLoop;
    }
    getInitialVolume() {
        return this.initialVolume;
    }
    getVolume() {
        return this.Audio.volume;
    }
    isPlaying() {
        return this.playing;
    }
    play() {
        this.Audio.pause();
        this.Audio.currentTime = 0;
        this.Audio.play();
        this.playing = true;
    }
    resetVolume() {
        this.Audio.volume = this.getInitialVolume();
    }
    setVolume(value) {
        this.Audio.volume = value;
    }
    stop() {
        this.Audio.pause();
        this.Audio.currentTime = 0;
        this.resetVolume();
        this.playing = false;
    }
}

class AudioStorage {
    constructor() {
        this.Sounds = {};
    }
    add(name, fileUrl, options = {}) {
        this.Sounds[name] = new Sound(fileUrl, options);
    }
    find(name) {
        return this.Sounds[name];
    }
}

class AudioMixer {
    constructor() {
        this.Storage = new AudioStorage();
        this.Channels = {
            Master: new AudioChannel(),
        };
    }
    createChannel(name, volume = 1) {
        this.Channels[name] = new AudioChannel(volume);
    }
    load() {
        for (let i of arguments) {
            this.Storage.add(i.name, i.url, i.options ? i.options : {});
        }
    }
    playSound(channelName, name) {
        const masterVolume = this.Channels.Master.getVolume();
        const channelVolume = channelName !== 'Master' ? this.Channels[channelName].getVolume() * masterVolume : masterVolume;
        const Sound = this.Storage.find(name);
        Sound.setVolume(Sound.getInitialVolume() * channelVolume);
        Sound.play();
    }
    setChannelVolume(channelName, value) {
        this.Channels[channelName].setVolume(value);
    }
    stopSound(name) {
        const Sound = this.Storage.find(name);
        Sound.stop();
    }
}

export { AudioChannel, AudioMixer, AudioStorage, Sound };
